@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
.card-container {
	display: flex;
	flex-direction: column;
	width: 17rem;
	height: 20rem;
	align-items: center;
	justify-content: center;
	background: rgb(64, 137, 247);
	padding: 1rem;
	margin: 1rem;
	border-radius: 10px;
	cursor: pointer;
	-moz-osx-font-smoothing: grayscale;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	transform: translateZ(0);
	transition: transform 0.25s ease-out;
}

.card-container img {
	width: 180px;
	height: 180px;
}

.card-container h1 {
	font-size: 22px;
	margin-bottom: 0;
	color: rgb(0, 0, 0, 1);
}

.card-container h2 {
	font-size: 20px;
	color: rgb(0, 0, 0, 0.7);
}

.card-container:hover {
	transform: scale(1.05);
}

.card-list {
	display: grid;
	grid-template-columns: repeat(4, 1.5fr);
}

.searchBox {
	padding: 6px;
	border: none;
	margin-top: 8px;
	margin-right: 16px;
	font-size: 17px;
}

body {
	background: #3e90a8;
}

.App {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-family: 'Poppins', sans-serif;
}

::-webkit-scrollbar {
	width: 12px;
	border-radius: 10px;
}

#Heading {
	font-family: 'Calligraffitti', cursive;
	letter-spacing: 0.2em;
	font-weight: 700;
}

::-webkit-scrollbar-thumb {
	background: linear-gradient(to top, #fcb045, #fd1d1d, #833ab4);
}

